import React from "react";
import Particles from "react-particles-js";
import ReactTypingEffect from "react-typing-effect";

const Header = () => {
  return (
    <div
      className="elegant-color-dark position-relative d-flex justify-content-center align-items-center"
      style={{ height: "100vh" }}
    >
      <Particles
        className="position-absolute w-100 h-100 zIndex-1000"
        params={{
          particles: {
            number: {
              value: 180,
              density: {
                enable: true,
                value_area: 1400,
              },
            },
            color: {
              value: "#ffffff",
            },
            shape: {
              type: "circle",
              stroke: {
                width: 1,
                color: "#000000",
              },
              polygon: {
                nb_sides: 5,
              },
              image: {
                src: "img/github.svg",
                width: 100,
                height: 100,
              },
            },
            opacity: {
              value: 0.1,
              random: false,
              anim: {
                enable: false,
                speed: 1,
                opacity_min: 0.1,
                sync: false,
              },
            },
            size: {
              value: 0,
              random: true,
              anim: {
                enable: false,
                speed: 20,
                size_min: 0.1,
                sync: false,
              },
            },
            line_linked: {
              enable: true,
              distance: 120,
              color: "#ffffff",
              opacity: 0.4,
              width: 1,
            },
            move: {
              enable: true,
              speed: 1,
              direction: "none",
              random: false,
              straight: false,
              out_mode: "out",
              bounce: false,
              attract: {
                enable: false,
                rotateX: 600,
                rotateY: 1200,
              },
            },
          },
          interactivity: {
            detect_on: "canvas",
            events: {
              onhover: {
                enable: true,
                mode: "grab",
              },
              resize: true,
            },
            modes: {
              grab: {
                distance: 140,
                line_linked: {
                  opacity: 1,
                },
              },
              bubble: {
                distance: 400,
                size: 40,
                duration: 2,
                opacity: 8,
                speed: 3,
              },
              repulse: {
                distance: 200,
                duration: 0.4,
              },
              push: {
                particles_nb: 4,
              },
              remove: {
                particles_nb: 2,
              },
            },
          },
          retina_detect: true,
        }}
      />
      <h1 className="white-text text-center p-1" id="header">
        <ReactTypingEffect
          speed={100}
          eraseDelay={200}
          typingDelay={620}
          text={[
            "Hi",
            "Welcome!",
            "I'm Brian Binag",
            "I'm a Web Developer",
          ]}
        />
      </h1>
    </div>
  );
};

export default Header;
